.main {
    width: 100%;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #000;
    &:hover {
        cursor: pointer;
        background-color: rgba(170, 236, 238, 0.904);
    } 
}

span.id {
    display: flex;
    align-items: center;
    padding: 15px;
    height: 100%;
}

.info {
    display: flex;
    .markerInfo {
        height: 100%;
        padding: 15px 0;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        .coordinates {
            h4 {
                font-size: 14px;
            }
            span {
                font-weight: 700;
            }
        }
    }
}

.actions {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    img {
        width: 30px;
        opacity: 0.8;
        &:hover {
            opacity: 1;
            background-color: #fff;
        }
    }
}
